.searchPage__header{
    display: flex;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgrey;
}
.searchPage__header img{
height: 50px;
margin-right: 50px;
}
.searchPage__options{
    display: flex;
    align-items: center;
    color:gray;
    margin-top: 30px;
}
.searchPage__options a{
    text-decoration: none;
    color: gray;
    margin-left: 5px

}
.searchPage__optionsleft , .searchPage__optionsright {
    display: flex;
    align-items: center;

}
.searchPage__option{
display: flex;
align-items: center;
margin-right: 20px;
}
.searchPage__optionsright{
    margin-left: 80px;
} 
  .searchPage  .search__input {
    margin-top: 0;
    margin: 0px 0px;

 }
 .searchPage__resultCount{
     color: #70757a;
     font-size: 14px;
 }
 searchPage__result{
     margin: 40px 0px;
 }
 .searchPage__resultImage{
     height: 50px;
     width: 50px;
     object-fit: contain;
     margin-right: 10px;
 }
 .searchPage__resultlink{
     display: flex;
     align-items: center;
     text-decoration: none;
     color: #000;
     margin-bottom: 3px;
 }
 .searchPage__resultTitle{
    text-decoration: none;
    
 }
 .searchPage__resultTitle:hover{
    text-decoration: underline;
    
 }
 .searchPage__resultTitle>h2{
    font-weight: 500;
    
 }
 .searchPage__resultdescription{
     margin-top: 10px;
 }
 .searchPage__results{
     max-width: 650px;
     margin-top: 20px;
     margin-left: 240px;
     margin-bottom: 100px;
 }